const getters = {
	user: (state) => {
		return state.user;
	},
    token:(state)=>{
	    return state.token;
    },
    pageId:(state)=>{
	    return state.pageId;
    },
    leftSideData:(state)=>{
	    return state.leftSideData;
    },
    handleInfo:(state)=>{
        return state.handleInfo;
    }
};
export default getters;



