<!--获取图标-->
<template>
    <div>
        <svg class="icon "  aria-hidden="true" :style="{width:iconWidth+'px',height:iconHeight+'px',color:iconColor}">
            <use :xlink:href="getIconName"></use>
        </svg>
        <slot></slot>
    </div>
</template>

<script>
    import "../../static/iconfont/iconfont.js";
    export default {
        name: "iconSymbol",
        props: {
            iconName: {
              type: String,
              required: true
            },
            iconWidth:{
              type: Number,
              default:15
            } ,
            iconHeight:{
              type: Number,
              default:15
            },
            iconColor:{
              type:String,
            }
        },
        computed: {
            getIconName() {
                return "#icon-"+this.iconName;
            }
        }
    };
</script>

<style lang="scss">
    @import "../../static/iconfont/iconfont.css";
    .icon {
        vertical-align: -0.15em;
        fill: currentColor;
        overflow: hidden;
    }
</style>



