<!--公共左侧菜单最多三级菜单-->
<template>
    <div class="asideArea">
<!--        首页不存在子菜单 不显示左侧导航栏 secData.length==0-->
        <el-menu
                class="el-menu-vertical-demo"
                :unique-opened="true"
                default-active="0-0"
                :collapse-transition="false"
                :key="refreshNum"
        >
            <div v-for="(menu,index) in leftSideData" :key=index>
                <!--               第1级存在2级子菜单-->
                <el-submenu v-if="menu.child" :index="String(index)">
                    <template slot="title">
                        <iSymbol :iconName="String(menu.icon)" style="display: inline-block"></iSymbol>
                        <span>{{menu.name}}</span>
                    </template>

                    <div v-for="(secMenu,secIndex) in menu.child" :key=secIndex>
                        <!--               第2级存在3级子菜单-->
                        <el-submenu v-if="secMenu.child" :index="index+'-'+secIndex">
                            <template slot="title">{{secMenu.name}}</template>
                            <el-menu-item
                                    v-for="(thiMenu,thiIndex) in secMenu.child"
                                    @click="jumpPath(thiMenu.path)"
                                    :key="thiMenu.path"
                                    :index="index+'-'+secIndex+'-'+thiIndex"
                            >{{thiMenu.name}}
                            </el-menu-item>
                        </el-submenu>
                        <!--               第2级不存在3级子菜单-->
                        <el-menu-item-group v-else>
                            <template slot="title"></template>
                            <el-menu-item
                                    :index="index+'-'+secIndex"
                                    @click="jumpPath(secMenu.path)"
                                    :key="secMenu.path"
                            >
                                <iSymbol :iconName="String(secMenu.icon)" style="display: inline-block"></iSymbol>
                                <span v-if="!secMenu.child">{{secMenu.name}}</span>
                            </el-menu-item>
                        </el-menu-item-group>
                    </div>
                </el-submenu>
                <!--               第1级不存在2级子菜单-->
                <el-menu-item v-else :index="String(index)" @click="jumpPath(menu.path)" :key="menu.path">
                    <iSymbol :iconName="String(menu.icon)" style="display: inline-block"></iSymbol>
                    <span slot="title">{{menu.name}}</span>
                </el-menu-item>
            </div>
        </el-menu>
    </div>
</template>
<script>
    export default {
        name: "AsideLeft",
        data() {
            return {
                // routeName:this.$route,
                // isCollapse: true,
                refreshNum: 0,
            };
        },
        created() {
        },
        mounted() {
        },
        computed:{
            leftSideData:{
                get(){
                   return this.$store.getters.leftSideData;
                },
                set(){

                }
            }
        },
        watch: {
            leftSideData(val) {
                this.refreshNum += 1;
            }
        },
        methods: {
            jumpPath: function (path) {
                if (path) {
                    this.$router.push({path: path, params: {id: "1111"}});
                }
            },
            // runAsync: function () {
            //     let p = new Promise(function (resolve, reject) {
            //         let timeOut = Math.random() * 2;
            //         if(timeOut>1){
            //             setTimeout(function () {
            //                 // console.log('执行完成');
            //                 resolve("执行成功");
            //             }, 1000);
            //         }else{
            //             setTimeout(function () {
            //                 // console.log('执行失败');
            //                 reject('执行错误');
            //             }, 1000);
            //         }
            //
            //     });
            //     return p;
            // },
        },
        components:{
        }
    };
</script>
<style lang="scss">
    .asideArea {
        flex: 0 0 auto;
        width: 200px;
        min-height: calc(100vh - 62px);
        .el-menu-vertical-demo:not(.el-menu--collapse) {
            width: 200px;
            min-height: calc(100vh - 62px);
            margin: 0;
            padding: 0;
            .el-menu-item.is-active{
                background-color: #3a7bd3;
                color: white;
                /*font-weight: 600;*/
            }
        }
        .el-menu-item-group__title {
            padding: 0;
            color: #62b4ff;
        }
    }
</style>




