<template>
    <div >
<!--        <iSymbol iconName="suo"></iSymbol>-->
        <Header></Header>
        <div style="display: flex;min-width: 1290px">
            <AsideLeft v-if='leftShow'></AsideLeft>
            <router-view style="flex: 1 1 auto; background-color: #f3f4fc;overflow-x: scroll"></router-view>
        </div>

    </div>
</template>

<script>
    import AsideLeft from "./asideLeft"
    import Header from "./header"
    export default {
        name: "full",
        data() {
            return {
            };
        },
        components: {
            AsideLeft: AsideLeft,
            Header: Header,
        },
        created(){
        },
        computed:{
          leftShow:{
              get(){
                  if(this.$store.getters.leftSideData.length>0){
                      return true
                  }
                  else {
                      return false
                  }
              }
              ,set(){}

          }
        },
        methods:{

        }

    }
</script>
<style>
</style>



