import Vue from 'vue';
import Router from 'vue-router';
import full from '@/components/full';
const login = () => import('../view/login');
const homePage = () => import('../view/homePage');
const personalInfo = () => import('../view/personManagement/personalInfo');
const personalContract = () => import('../view/personManagement/personalContract');
const personalDimission = () => import('../view/personManagement/personalDimission');
const carInfo = () => import('../view/carManagement/carInfo');
const carExamine = () => import('../view/carManagement/carExamine');
const carScraped = () => import('../view/carManagement/carScraped');
const page404 = () =>import('../components/page404')
Vue.use(Router);
export const constantRouterMap = [
	{
		path: '/login',
		name: 'login',
		component: login
	},
	 {
    		path: '/',
    		component: full,
    		children: [
    			{
    				path: '/',
    				name: 'homePage',
    				component: homePage
    			},
    			]}

];
export const asyncRouterMap = [
    {
		path: '/',
		component: full,
		children: [
			{
				path: '/',
				name: 'homePage',
				component: homePage
			},
			{
            		path: 'personalInfo',
            		name: 'personalInfo',
            	     meta: { pageId: '101',title:'人员档案' },
            		component: personalInfo,
            	},
            	{
            		path: 'personalDimission',
            		name: 'personalDimission',
            		  meta: { pageId: '102',title:'人员离职'},
            		component: personalDimission,
            	},
            	{
            		path: 'personalContract',
            		name: 'personalContract',
            		  meta: { pageId: '103' },
            		component: personalContract
            	},
            	{
            		path: 'carExamine',
            		  meta: { pageId: "201" },
            		name: 'carExamine',
            		component: carExamine
            	},
            	{
            		path: 'carInfo',
            		 meta: { pageId: '202'},
            		name: 'carInfo',
            		component: carInfo
            	},
            	{
            		path: 'carScraped',
            		meta: { pageId: '203' },
            		name: 'carScraped',
            		component: carScraped
            	},
            	{ path: '*', component:page404  }
		]
	 },

];
export default new Router({
	routes: constantRouterMap
});



