<!--公共头部-->
<template>
    <div class="headArea">
        <div style="width:150px;text-align: left;flex:1 1 auto;">
            <img src="../../static/img/companyLogo.png" style="height: 35px;padding-left: 10px "/></div>
        <el-menu class="el-menu-demo" mode="horizontal" style="flex:0 0 auto;" text-color="white">
<!--            v-show menu.name控制头部菜单-->
            <el-menu-item v-for="(menu,index) in headData" :index="String(index)" class="head_menu_item" :key="index" v-show="showMenu.indexOf(menu.name)>-1"
                          @click="setChildMenu(menu.child,menu.path)">
                <iSymbol :iconName="String(menu.icon)" style="height: 20px;" :iconWidth="25" :iconHeight="25"></iSymbol>
                <div style="height: 20px;font-size: 13px" >{{menu.name}}</div>
            </el-menu-item>
        </el-menu>
        <div style="width:160px;text-align: center;flex:0 0 auto;">
            <iSymbol iconName="user" style="height: 30px;display: inline-block" :iconWidth="35"
                     :iconHeight="35"></iSymbol>
            <span style="font-size: 16px ">LYQ</span>
            <span @click="">下拉</span>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Header",
        components: {},
        data() {
            return {
                username: "LYQ",
                isFocus: "",
                showMenu:["人事管理","首页","车辆管理"],
                webSocket: '',//webSocket 实例
                headData: [
                    {
                        name: "首页",
                        path: "/",
                        // btnId:'',
                        icon: 'user',
                        child: [],
                    },
                    {
                        name: "人事管理",
                        path: "/personalInfo",
                        icon: "personManage",
                        child: [
                            {
                                name: "人员管理", path: "/personalInfo", icon: "personManage",
                                child: [
                                    {
                                        name: "人员档案", path: "/personalInfo", icon: "personFiles"
                                    },
                                    {
                                        name: "人员合同", path: '/personalContract', icon: 'contract'
                                    },
                                    {
                                        name: "人员离职", path: '/personalDimission', icon: 'dimission'
                                    }
                                ]
                            },
                            {
                                name: "系统配置", path: "/personalInfo", icon: "sysManage",
                                child: [
                                    // {
                                    //     name: "组织管理", path: "/personalInfo", icon: "organization",
                                    // }
                                ]

                            }
                        ]
                    },
                    {
                        name: "车辆管理",
                        path: "/carInfo",
                        icon: 'vehicleManage',
                        child: [
                            {
                                name: '车辆管理', path: "/carInfo", icon: 'vehicleManage',
                                child: [
                                    {name: "车辆档案", icon: "vehicleFiles", path: "/carInfo"},
                                    {name: "车辆报废", icon: "scraped", path: "/carScraped"},
                                    {name: "车辆年审", icon: "examine", path: "/carExamine"},
                                ]
                            },

                        ],

                    },
                    {
                        name: "线路管理",
                        path: "/carInfo",
                        icon: 'lines',
                        child: [
                            {
                                name: '线路管理', path: "/carInfo", icon: 'lines',
                                child: [
                                    {name: "车辆档案", icon: "vehicleFiles", path: "/carInfo"},
                                ]
                            },

                        ],

                    },
                    {
                        name: "能源管理",
                        path: "/carInfo",
                        icon: 'energy',
                        child: [
                            {
                                name: '能源管理', path: "/carInfo", icon: 'energy',
                                child: [
                                    {name: "车辆档案", icon: "vehicleFiles", path: "/carInfo"},
                                ]
                            },

                        ],

                    },
                    {
                        name: "计划管理",
                        path: "/carInfo",
                        icon: 'plan',
                        child: [
                            {
                                name: '计划管理', path: "/carInfo", icon: 'plan',
                                child: [
                                    {name: "车辆档案", icon: "vehicleFiles", path: "/carInfo"},
                                ]
                            },

                        ],

                    },
                    {
                        name: "生产管理",
                        path: "/carInfo",
                        icon: 'produce',
                        child: [
                            {
                                name: '生产管理', path: "/carInfo", icon: 'produce',
                                child: [
                                    {name: "车辆档案", icon: "vehicleFiles", path: "/carInfo"},
                                ]
                            },

                        ],

                    },
                    {
                        name: "收入管理",
                        path: "/carInfo",
                        icon: 'income',
                        child: [
                            {
                                name: '收入管理', path: "/carInfo", icon: 'income',
                                child: [
                                    {name: "车辆档案", icon: "vehicleFiles", path: "/carInfo"},
                                ]
                            },

                        ],

                    },
                    {
                        name: "安全管理",
                        path: "/carInfo",
                        icon: 'safety',
                        child: [
                            {
                                name: '安全管理', path: "/carInfo", icon: 'safety',
                                child: [
                                    {name: "车辆档案", icon: "vehicleFiles", path: "/carInfo"},
                                ]
                            },

                        ],

                    },
                    {
                        name: "维保管理",
                        path: "/carInfo",
                        icon: 'maintain02',
                        child: [
                            {
                                name: '维保管理', path: "/carInfo", icon: 'maintain02',
                                child: [
                                    {name: "车辆档案", icon: "vehicleFiles", path: "/carInfo"},
                                ]
                            },

                        ],

                    },
                    {
                        name: "物资管理",
                        path: "/carInfo",
                        icon: 'materials',
                        child: [
                            {
                                name: '物资管理', path: "/carInfo", icon: 'materials',
                                child: [
                                    {name: "车辆档案", icon: "vehicleFiles", path: "/carInfo"},
                                ]
                            },

                        ],

                    },
                    {
                        name: "统计管理",
                        path: "/carInfo",
                        icon: 'statistics',
                        child: [
                            {
                                name: '统计管理', path: "/carInfo", icon: 'statistics',
                                child: [
                                    {name: "车辆档案", icon: "vehicleFiles", path: "/carInfo"},
                                ]
                            },

                        ],

                    },
                    {
                        name: "分公司",
                        path: "/carInfo",
                        icon: 'company',
                        child: [
                            {
                                name: '分公司', path: "/carInfo", icon: 'company',
                                child: [
                                    {name: "车辆档案", icon: "vehicleFiles", path: "/carInfo"},
                                ]
                            },

                        ],

                    },
                ]
            }
        },
        created() {
            let leftSideData = JSON.parse(window.sessionStorage.getItem("leftSideData"));
            if (leftSideData && leftSideData.length > 0) {
                this.$store.dispatch("setLeftSideData", leftSideData)
            }
        }
        ,
        methods: {
            setChildMenu: function (obj, path) {
                this.$store.dispatch("setLeftSideData", obj);
                window.sessionStorage.setItem("leftSideData", JSON.stringify(obj))
                this.jumpPath(path);
            },
            // 页面跳转
            jumpPath: function (path) {
                if (path) {
                    this.$router.push({path: path, params: {id: '1111'}})
                }
            }

        }
    }
</script>

<style lang="scss">
    .headArea {
        width: 100%;
        min-width: 1290px;
        height: 61px;
        display: flex;
        background-color: #3a7bd3;
        justify-content: space-between;
        align-items: center;

        .head_menu_item {
            padding: 0 10px;
            background-color: #3a7bd3;
            display: flex;
            flex-direction: column;
            text-align: center;
        }

        .head_menu_item:hover {
            background-color: #3a7bd3 !important;

        }

        .el-menu.el-menu--horizontal {
            border-bottom: none;
        }
    }


</style>



