<template>
    <div id="app">
        <router-view/>
    </div>
</template>

<script>
    import webSocket from './utils/webSocket'

    export default {
        name: "App",
        created() {
            if ('WebSocket' in window) {
                this.websocket = new WebSocket('ws://127.0.0.1:8080/BusERP/WebSocket/CYERP6/1464')
                webSocket.initWebSocket(this)
                this.websocket.onmessage = (event) => {
                    let carouselData = [];
                    let arr = JSON.parse(event.data).List;
                    let listLen = arr.length;
                    if (listLen > 0) {
                        for (let i = 0; i < Math.ceil(listLen / 6); i++) {
                            carouselData[i] = [];
                            for (let j = 0; j < 6; j++)
                                if (arr[i * 6 + j]) {
                                    carouselData[i][j] = arr[i * 6 + j];
                                    // this.$store.dispatch("setHandleInfo", carouselData)
                                } else {
                                    this.$store.dispatch("setHandleInfo", carouselData)
                                    return false;
                                }
                        }
                    }

                }

            } else {
                alert('当前浏览器 Not support websocket')
            }
        }
    };
</script>

<style lang="scss">
    #app {
        /*font-family: "Avenir", Helvetica, Arial, sans-serif;*/
        font-family: "微软雅黑";
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        color: #2c3e50;
        width: 100%;
        min-height: 100vh;
    }

    html,
    body,
    div {
        margin: 0;
        padding: 0;
        font-size: 15px;
    }

    html {
        min-width: 1200px;
        /*min-height: 650px;*/
    }
    /*表格插件样式*/
    .el-table__body td {
        padding: 0;
        text-align: center;
        white-space: nowrap;
    }
    .el-table th {
        padding: 0;
        text-align: center;
        font-weight: 500;
        color: black;

    }
    .el-table .cell, .el-table th div {
        padding: 0!important;
    }
    th.gutter {
        display: block !important;
    }
    tr.current-row > td {
        background-color: #62b4ff!important;
        color: white;
    }

    /*遮罩层*/
    .shadow_layer {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0px;
        opacity: 0.5;
        background-color: #bcaea0;
        z-index: 100;
    }
    /*页面导航定位*/
    .page_position {
        width: 100%;
        height: 30px;
        line-height: 30px;
        text-indent: 20px;
        background-color: #e6e3fd;
    }
    ::-webkit-scrollbar {
        /*滚动条整体样式*/
        width: 7px; /*高宽分别对应横竖滚动条的尺寸*/
        height: 8px;
    }

    ::-webkit-scrollbar-thumb {
        /*滚动条里面小方块*/
        border-radius: 3px;
        -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0);
        background: gray;
    }

    ::-webkit-scrollbar-track {
        /*滚动条里面轨道*/
        -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0);
        border-radius: 5px;
        background: #ececec;
    }

    /*页面搜索模块样式*/
    .area_search {
        width: calc(100% - 60px);
        height: 115px;
        padding: 10px 20px 10px 30px;
        /*组织树选中颜色*/
        .el-tree--highlight-current .el-tree-node.is-current > .el-tree-node__content {
            color: #5e68ff;
        }

        /*下拉框*/
        .el-select-dropdown {
            min-width: calc(16% - 35px) !important;
        }

        /*输入框高度*/
        .el-input__inner {
            height: 35px;
            line-height: 35px;
        }

        .search_title {
            width: 100%;
            height: 15px;
            margin: 10px 0;
            line-height: 15px;
            border-left: 4px solid #3a7bd3;
            text-indent: 10px;

        }

        .search_item_tree {
            width: 33%;
            height: 35px;
            line-height: 35px;
            float: left;
            margin: 5px 0;

            .item_title_tree {
                width: 25%;
                text-align: right;
                overflow: hidden;
                float: left;
            }
            .item_input_tree {
                width: 60%;
                padding: 0 12% 0 2%;
                height: 35px;

                .el-button {
                    padding: 12px 9px;
                }
            }
            .dpm_tree {
                float: left;
                margin: 0 0 0 27%;
                min-width: 50%;
                z-index: 200;
                border: 1px solid lightgrey;
                border-radius: 5px;
            }
        }
        .search_item {
            display: flex;
            width: 33%;
            height: 35px;
            line-height: 35px;
            float: left;
            margin: 5px 0;

            .item_title {
                width: 25%;
                flex: 0 0 auto;
                text-align: right;
                overflow: hidden;
            }
            .item_time {
                width: 60%;
                margin: 0 12% 0 2%;
                /*height: 35px;*/
                /*line-height: 35px;*/
                /*时间插件图片位置*/
                .el-range__icon {
                    line-height: 26px;
                }
                .el-range-input {
                    font-size: 13px;
                    width: 50%;
                }

                .el-range-separator {
                    font-size: 13px;
                    width: 3%;
                    line-height: 26px;
                }

                .el-input__prefix {
                    left: 4px;
                    top: -2px;
                }
            }
            .item_select{
                width: 60%;
                margin: 0 12% 0 2%;
            }
            .item_input {
                width: 60%;
                padding: 0 12% 0 2%;
                height: 35px;
            }
        }
    }
    /*页面表格模块样式*/
    .area_table {
        width: calc(100% - 60px);
        padding: 0px 30px;
        /*表格样样式*/
        .el-table th {
            background-color: #62b4ff;
        }

        .table_cont {
            .el-table .cell {
                height: 30px;
                line-height: 30px;
            }

            tr.current-row > td:last-child {
                background-color: white !important;
                color: white;
            }
        }

        .table_head {
            width: 100%;
            padding: 10px 0;
            display: flex;
            justify-content: space-between;
            align-items: center;

            .table_title {
                height: 15px;
                line-height: 15px;
                border-left: 4px solid #3a7bd3;
                text-indent: 10px;
            }
        }
    }
</style>



